import interfaces from '../../utils/interfaces.js';
var common = require('../../utils/common.js');
import app from "../../App.vue";
export default {
	data() {
		return {
			tab:{
				name:["最新订单","一口价","报价"],
				value:["new","1","2"]
			},
			tab_index:0,
			master_info: '',
			tip: '',
			sum: 0,
			list: [],
			page: 1,
			complete: true,
		}
	},
	onLoad(options) {
		this.type=options.type?options.type:0;
		this.tab_index=this.type;
	},
	onShow() {
		this.master_info = app.globalData.master_info;
		if(app.isAuth()){
			this.loadData(1);
		}
	},
	/**
	 * 页面上拉触底事件的处理函数
	 */
	onReachBottom() {
		if (!this.complete) {
			this.page++;
			this.loadData(this.page);
		}
	},
	methods: {
		tabChange(e) {
			this.tab_index=e.detail.value;
			this.type=this.tab.value[this.tab_index];
			this.loadData(1);
		},
		loadData(page) {
			this.request({
				url: interfaces.orderHall,
				data: {
					page: page,
					type: this.type
				},
				header: {
					token: this.master_info.token
				},
				method: 'POST',
				success: res => {
					console.log(res);
					if (res.code == 200) {
						
						if (page == 1) {
							this.list = [];
						}
			
						this.sum=res.sum;
			
						if (!common.isBlank(res.data)) {
							if (res.data.length < res.num) {
								this.complete = true;
								this.tip = '数据已全部加载，暂无其他数据';
							} else {
								this.complete = false;
								this.tip = '上拉加载更多';
							}
			
							for (var k in res.data) {
								res.data[k].create_time_txt = common.formatDT(res.data[k].create_time, 'yyyy.MM.dd h:m');
							}
			
							this.list = this.list.concat(res.data);
							this.page = page;
			
			
						} else {
							this.complete = true;
							this.tip = '数据已全部加载，暂无其他数据';
						}
			
					} else {
						app.isLogin(res.code, res.msg);
					}
				}
			})
		},
		//跳转订单详情页
		goDetail(id) {
			wx.navigateTo({
				url: 'halldetail?id=' + id,
			})
		},

	}
}
